<template>
  <div id="app">
    <div v-if="loading" class="loader loader-default is-active" data-text="地圖生成中!" data-blink></div>
    <div class="row no-gutters">
      <div class="col-sm-3">
        <div class="toolbox">
          <div class="sticky-top bg-info shadow-sm p-2">
            <div class="form-group d-flex">
              <label class="mr-2 col-form-label text-right text-white" for="cityName">縣市</label>
              <div class="flex-fill">
                <select id="cityName" class="form-control"
              v-model="select.city" @change="removeMarker(); updateMap()">
                <option value="">-- Select One --</option>
                <option :value="c.CityName" v-for="c in cityName" :key="c.CityName">
                  {{ c.CityName }}
                </option>
              </select>
              </div>
            </div>
            <div class="form-group d-flex">
              <label class="mr-2 col-form-label text-right text-white" for="area">地區</label>
              <div class="flex-fill">
                <select class="form-control" id="area"  v-model="select.area" @change="removeMarker(); s_updateMap() ">
                  <option selected>-- Select One --</option>
                  <option :key="corner.AreaName" :value="corner.AreaName" 
                  v-for="corner in cityName.find((city)=> city.CityName === select.city).AreaList" >{{ corner.AreaName}}</option>
                </select>
              </div>
            </div>
          </div>
          <div class="list-group" >
            <ul class="list-group">
                <template v-for="(item, key) in data">
                  <a class="left-item  -group-item text-left" :key="key"
                    v-if="item.properties.county === select.city
                      && item.properties.town === select.area"
                    :class="{ 'highlight': item.properties.mask_adult || item.properties.mask_child}"
                    @click="penTo(item)">
                    <h3>{{ item.properties.name }}</h3>
    
                    <p class="mb-0">電話：<a>{{ item.properties.phone }}</a></p>
                    <p class="mb-0">小提醒：<a>{{ item.properties.note }}</a></p>
                    <p class="mb-4">地址：<a :href="`https://www.google.com.tw/maps/place/${item.properties.address}`"
                      target="_blank" title="Google Map">
                      {{ item.properties.address }}</a>
                    </p>
                    <p class="mb-5">
                      <button type="button" class="btn btn-warning btn-block">成人口罩：{{ item.properties.mask_adult}}   </button>
                      <button type="button" class="btn btn-info btn-block">兒童口罩：{{ item.properties.mask_child}}</button>
                      <!-- 成人口罩：{{ item.properties.mask_adult}} | 兒童口罩：{{ item.properties.mask_child}} -->
                    </p>
                  </a>
              </template>
            </ul>
          </div>
        </div>
      </div>
      <div class="col-sm-9">
        <div id="map"></div>
      </div>
    </div>
  </div>
</template>

<script>
import L from 'leaflet';
import cityName from './assets/cityName.json';

let osmMap = {};
const goldIcon = new L.Icon({
        iconUrl: 'https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-gold.png',
        shadowUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/0.7.7/images/marker-shadow.png',
        iconSize: [25, 41],
        iconAnchor: [12, 41],
        popupAnchor: [1, -34],
        shadowSize: [41, 41]
      });

      const redIcon = new L.Icon({
        iconUrl: 'https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-red.png',
        shadowUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/0.7.7/images/marker-shadow.png',
        iconSize: [25, 41],
        iconAnchor: [12, 41],
        popupAnchor: [1, -34],
        shadowSize: [41, 41]
      });

export default {
  name: 'App',
  data: () => ({
    data: [],
    cityName,
    select: {
      city: '臺北市',
      area: '大同區'
    },
    loading: true,
  }),
  methods: {
    updateMap() {
      const pharmacies = this.data.filter((pharmacy) => (
        pharmacy.properties.county === this.select.city));
      this.select.area = pharmacies[0].properties.town
      const test = pharmacies.filter((item) => (item.properties.town === this.select.area ));
      test.forEach((pharmacy) => {
        const { properties, geometry } = pharmacy;
        var color = properties.mask_adult ? goldIcon :redIcon
        L.marker([geometry.coordinates[1],geometry.coordinates[0]],{icon: color})
          .addTo(osmMap).bindPopup(
          `<strong>${properties.name}</strong> <br>
          口罩剩餘：<strong>成人 - ${properties.mask_adult ? `${properties.mask_adult} 個` : '未取得資料'}/ 兒童 - ${properties.mask_child ? `${properties.mask_child} 個` : '未取得資料'}</strong><br>
          地址: <a href="https://www.google.com.tw/maps/place/${properties.address}" target="_blank">${properties.address}</a><br>
          電話: ${properties.phone}<br>
          <small>最後更新時間: ${properties.updated}</small>`);
      });
     
      this.penTo(pharmacies[0])
    },

    s_updateMap() {
      const pharmacies = this.data.filter((pharmacy) => (
        pharmacy.properties.county === this.select.city & pharmacy.properties.town === this.select.area));
      pharmacies.forEach((pharmacy) => {
        const { properties, geometry } = pharmacy;
        var color = properties.mask_adult ? goldIcon :redIcon
        L.marker([geometry.coordinates[1],geometry.coordinates[0]],{icon: color})
          .addTo(osmMap).bindPopup(
          `<strong>${properties.name}</strong> <br>
          口罩剩餘：<strong>成人 - ${properties.mask_adult ? `${properties.mask_adult} 個` : '未取得資料'}/ 兒童 - ${properties.mask_child ? `${properties.mask_child} 個` : '未取得資料'}</strong><br>
          地址: <a href="https://www.google.com.tw/maps/place/${properties.address}" target="_blank">${properties.address}</a><br>
          電話: ${properties.phone}<br>
          <small>最後更新時間: ${properties.updated}</small>`);
      });
     
      this.penTo(pharmacies[0])
    },

    removeMarker() {
      osmMap.eachLayer((layer) => {
        if (layer instanceof L.Marker) {
          osmMap.removeLayer(layer);
        }
      });
    },
    penTo(item) {
      const {  properties,geometry } = item;
      osmMap.panTo([geometry.coordinates[1],geometry.coordinates[0]])
      var color = properties.mask_adult ? goldIcon :redIcon
        L.marker([geometry.coordinates[1],geometry.coordinates[0]],{icon: color,})
          .addTo(osmMap).bindPopup(
          `<strong>${properties.name}</strong> <br>
          口罩剩餘：<strong>成人 - ${properties.mask_adult ? `${properties.mask_adult} 個` : '未取得資料'}/ 兒童 - ${properties.mask_child ? `${properties.mask_child} 個` : '未取得資料'}</strong><br>
          地址: <a href="https://www.google.com.tw/maps/place/${properties.address}" target="_blank">${properties.address}</a><br>
          電話: ${properties.phone}<br>`).openPopup() ;

    },
    removeloading(){
      this.loading = false
    }
  },
  mounted() {
    const url = 'https://raw.githubusercontent.com/kiang/pharmacies/master/json/points.json';
    this.$http.get(url).then((response) => {
      this.data = response.data.features;
      console.log(typeof(response.status))
      if(response.status == 200){
        this.removeloading()
        console.log(this.loading)
      }
      this.updateMap();
      
    });

    osmMap = L.map('map', {
      center: [25.0093258, 121.5239756],
      zoom: 20,
    });

    L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
      attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
    }).addTo(osmMap);
 
  },
};
</script>

<style lang="scss">
@import 'bootstrap/scss/bootstrap';
@import 'pure-css-loader/dist/css-loader';

body,button, input, select, textarea,h1 ,h2, h3, h4, h5, h6 { font-family: Microsoft YaHei,'宋体' , Tahoma, Helvetica, Arial, "\5b8b\4f53", sans-serif;}
#map{
  height: 100vh;
}

.highlight {
  background: #ffffff00;
}

.toolbox {
  height: 100vh;
  overflow-y: auto;
    a {
      cursor: pointer;
    }
}
.left-item{
  padding: 10px;
  border-radius: 15px;
}
.left-item:hover{
  background: rgb(230, 230, 230);
  border: 3px;
  
}
</style>
